.promo{
    color: #fff;
    &__shadow{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(90deg, #2B352F -31.52%, rgba(57, 65, 60, 0) 67.92%);
    }
    &__item{
        height: 786px;
    }
    &__content{
        position: relative;
        z-index: 2;
        padding: 150px 0 257px;
        width: 60%;
    }
    &__title{
        font-size: 77px;
        line-height: 95px;
        font-weight: 700;
        color: #fff;
        text-align: left;
    }
    &__subtitle{
        margin-top: 30px;
        margin-bottom: 55px;
        font-size: 27px;
        font-weight: 500;
    }
    &__mark{
        width: 100px;
        height: 100px;
        top: 49%;
        left: 39%;
        transform: translate(-100%, 100%);
    }
    &__scrollbar.swiper-pagination{
        bottom: 0;
        max-width: 316px;
        width: 100%;
        height: 4px;
        background-color: hsla(0,0%,100%,.5);
        .swiper-pagination-progressbar-fill{
            --swiper-theme-color: #17B8A8;
        }
    }
    &__arrow{
        bottom: -12px;
        top: auto;
    }
    .swiper-button-next{
        right: -15%;
    }
    .swiper-button-next:after{
        content: url('../icons/right.svg');
    }
    .swiper-button-prev{
        left: -15%;
    }
    .swiper-button-prev:after{
        content: url('../icons/left.svg');
    }
    &__bottom{
        position: absolute;
        z-index: 1;
        height: 93px;
        width: 316px;
        bottom: 0;
        width: 100%;
    }

    &__inner{

    }
}

.img{
    &-bg{
        width: 100%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 0;
    }
    &-underbg{
        position: absolute;
    }
}

.temperature{
    color: #000;
    background: rgba(255,255,255,0.5);
    padding: 14px 25px 14px 16px;
    width: 317px;
    right: 0;
    border-radius: 3px;
    *{
        display: inline-block;
    }
    p{
        margin: 0;
        font-size: 14px;
        font-weight: 700;
        margin-right: 10px;
    }
    span{
        font-size: 19px;
        font-weight: 700;
    }
    &__item{
        flex-basis: 45%;
        white-space: nowrap;
        text-align: center;
        &:first-child{
            border-right: 2px solid #17B8A8;
        }
    }
}

.scrollbar{
    position: relative;
    max-width: 316px;
    bottom: -25px;
}