.additional{
    background-color: #F5FEFD;
    padding: 110px 0;
    &__title{
        margin-top: 0;
    }
    &__sliders{
        &_col{
            margin-top: 100px;           
        }
    }
}

.white__arrow{
    top: 20%;
    border: 1px solid #FFFFFF;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    &::after{
        color: #fff;
        font-size: 15px;
    }
    &-left{
        left: 20px;
    }
    &-right{
        right: 20px;
    }
}

.tabs{
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    color: $color-tabs;
    font-size: 19px;
    padding: 0;

    &__item{
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 22px;
        margin: 0;
        margin-bottom: 31px;
        text-align: center;
        max-width: 318px;
        width: 316px;
        border: 2px solid #0DB2A2;
        border-radius: 5px;
        box-shadow: 0px 4px 40px rgba(204, 221, 219, 0.2);
        &_active{
            background-color: $accent;
            color: #fff;
        }
    }
    &__content{
        display: none;
        &_active{
            display: block;
        }
    }
}

.service{
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    /*Таблица*/
    &__title{
        color: $accent;
        font-weight: 700;
    }
    &__img{
        margin-right: 1.5em;
    }
    &__row{
        td{
            &:first-child{
                width: 70%;
                text-align: left;
            }
            width: 15%;
        }
    }
    &__text{
        padding: 0 10px;
        font-size: 0.85em;
        text-align: right;
    }
    &__price{
        font-size: 0.95em;
        font-weight: 700;
        text-align: right;
    }

    /*слайдер*/
    &-slide{
        &__item{
            background-color: $slide-green;
            border-radius: 0px 0px 10px 10px;
            overflow: hidden;
        }
        &__header{
            width: 100%;
            height: 295px;
            img{
                width: 100%;
                height: 100%;
            }
        }
        &__footer{
            color: #fff;
            padding: 45px 40px;
            min-height: 458px;
        }
        &__title{
            line-height: 45px;
        }
        &__icons{
            margin: 25px 0;
            &-img{
                margin-right: 15px;
                width: 40px;
                height: 40px;
                img{
                    width: 100%;
                }
            }
        }
        &__text{
            font-size: 17px;
            min-height: 75px;
        }
    }
}